<template>
  <div class="app-container">
    <div class="filter-container">
      <el-input
        v-model="listQuery.name"
        placeholder="名称"
        style="width: 200px;"
        class="filter-item"
        size="small"
        @keyup.enter.native="handleFilter"
      />
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        size="small"
        @click="handleFilter"
      >搜索</el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      stripe
      fit
      highlight-current-row
    >
      <el-table-column align="center" label="序号" width="95">
        <template slot-scope="scope">{{ scope.$index + 1 }}</template>
      </el-table-column>
      <el-table-column label="名称" align="center">
        <template slot-scope="scope">{{ scope.row.name }}</template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="230" class-name="small-padding fixed-width">
        <template slot-scope="{ row }">
          <el-button type="primary" size="mini" @click="handleUpdate(row)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination
      :total="total"
      :page.sync="listQuery.pageNo"
      :limit.sync="listQuery.pageSize"
      @pagination="fetchData"
    />
    <el-dialog title="编辑" :visible.sync="dialogFormVisible">
      <el-form
        class="container-form"
        ref="dataForm"
        :model="dataForm"
        :rules="rules"
        auto-complete="on"
        label-position="left"
        status-icon
      >
        <div class="title-container">
          <h3 class="title">白名单</h3>
        </div>
        <el-form-item prop="whitelist" label="IP白名单设置">
          <span class="svg-container">
            <svg-icon icon-class="user" />
          </span>
          <el-input
            v-model="dataForm.whitelist"
            placeholder="一行一个ip地址，如需取消，清空白名单即可"
            name="ip"
            :autosize="{ minRows: 10 }"
            type="textarea"
            tabindex="1"
          />
        </el-form-item>
        <el-button type="primary" @click="submitForm">保存</el-button>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { get, update, list } from "@/api/whitelist";
import waves from "@/directive/waves"; // waves directive
import { isValidIP } from "@/utils/validate";
import Pagination from "@/components/Pagination";

export default {
  components: { Pagination },
  directives: { waves },
  created() {
    this.fetchData();
  },
  data() {
    const checkIP = (rule, value, callback) => {
      if (value) {
        let split = value.split("\n");
        for (const ip of split) {
          if (!isValidIP(ip)) {
            callback(new Error("ip地址错误"));
          } else {
            callback();
          }
        }
      }
      callback();
    };
    return {
      total: 0,
      listQuery: {
        name: "",
        pageNo: 1,
        pageSize: 20
      },
      list: null,
      listLoading: true,
      dialogFormVisible: false,
      dataForm: {
        tenantId: undefined,
        name: "",
        whitelist: ""
      },
      rules: {
        whitelist: [{ validator: checkIP, trigger: "blur" }]
      }
    };
  },
  methods: {
    fetchData() {
      this.listLoading = true;
      list(this.listQuery).then(response => {
        this.list = response.data.record;
        this.total = response.data.total;
        this.listLoading = false;
      });
    },
    handleFilter() {
      this.listQuery.pageNo = 1;
      this.fetchData();
    },
    handleUpdate(row) {
      this.dataForm.tenantId = row.tenantId;
      this.dataForm.whitelist = row.whitelist;
      this.dataForm.name = row.name;
      this.dialogFormVisible = true;
    },
    submitForm() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          update(this.dataForm).then(response => {
            if (response.code === 0) {
              for (const v of this.list) {
                if (v.tenantId === this.dataForm.tenantId) {
                  const index = this.list.indexOf(v);
                  this.list.splice(index, 1, this.dataForm);
                  break;
                }
              }
              this.dialogFormVisible = false;
              this.$notify({
                title: "成功",
                message: "更新成功",
                type: "success",
                duration: 2000
              });
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
            }
          });
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  .container-form {
    position: relative;
    width: 520px;
    max-width: 100%;
    padding: 160px 35px 0;
    margin: 0 auto;
    overflow: hidden;
  }
}
</style>
