<template>
  <div class="container">
    <el-form
      class="container-form"
      ref="dataForm"
      :model="dataForm"
      :rules="rules"
      auto-complete="on"
      label-position="left"
      status-icon
    >
      <div class="title-container">
        <h3 class="title">白名单</h3>
      </div>
      <el-form-item prop="whitelist" label="IP白名单设置">
        <span class="svg-container">
          <svg-icon icon-class="user" />
        </span>
        <el-input
          v-model="dataForm.whitelist"
          placeholder="一行一个ip地址，如需取消，清空白名单即可"
          name="ip"
          :autosize="{ minRows: 10 }"
          type="textarea"
          tabindex="1"
        />
      </el-form-item>
      <el-button
        :loading="loading"
        type="primary"
        style="width:100%;margin-bottom:30px;"
        @click.native.prevent="submitForm"
        >保存
      </el-button>
    </el-form>
  </div>
</template>
<script>
import { get, update } from "@/api/whitelist";
import { isValidIP } from "@/utils/validate";

export default {
  created() {
    get().then(response => {
      if (response.code === 0) {
        const data = response.data;
        this.dataForm.whitelist = data.whitelist;
      }
    });
  },
  data() {
    const checkIP = (rule, value, callback) => {
      if (value) {
        let split = value.split("\n");
        for (const ip of split) {
          if (!isValidIP(ip)) {
            callback(new Error("ip地址错误"));
          } else {
            callback();
          }
        }
      }
      callback();
    };
    return {
      dataForm: {
        whitelist: ""
      },
      loading: false,
      rules: {
        whitelist: [{ validator: checkIP, trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          update(this.dataForm).then(response => {
            if (response.code === 0) {
              this.$notify({
                title: "成功",
                message: "更新成功",
                type: "success",
                duration: 2000
              });
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
            }
          });
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  .container-form {
    position: relative;
    width: 520px;
    max-width: 100%;
    padding: 160px 35px 0;
    margin: 0 auto;
    overflow: hidden;
  }
}
</style>
