<template>
  <div class="dashboard-container">
    <component :is="currentRole" />
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import adminWhitelist from "./admin";
import tenantWhitelist from "./tenant";

export default {
  name: "whitelist",
  components: { adminWhitelist, tenantWhitelist },
  data() {
    return {
      currentRole: "adminWhitelist"
    };
  },
  computed: {
    ...mapGetters(["roles"])
  },
  created() {
    if (!this.roles.includes("ADMIN")) {
      this.currentRole = "tenantWhitelist";
    }
  }
};
</script>
