import request from "@/utils/request";

export function list(params) {
  return request({
    url: "/whitelist/list",
    method: "post",
    headers: {
      "Content-Type": "application/json"
    },
    data: params
  });
}

export function get() {
  return request({
    url: "/whitelist",
    method: "get"
  });
}

export function update(params) {
  return request({
    url: "/whitelist/update",
    method: "post",
    headers: {
      "Content-Type": "application/json"
    },
    data: params
  });
}
